	module.exports = {"data":{"site":{"siteMetadata":{"title":"MyBlog"}},"markdownRemark":{"html":"<p>En este post voy a mostrar como crear tu página web para blog personal utilizando\nGatsby.</p>\n<p><a href=\"https://www.gatsbyjs.org\">Gatsby</a> es un generador de páginas estáticas con varias\nplantillas que puedes utilizar para crear tu proyecto. La ventaja de Gatsby es que\nlas páginas estáticas se pueden alojar en páginas web que no pueden ejecutar código\ndel lado del servidor como Github Pages. Además, puedes obtener contenido desde múltiples\nlugares gracias a que utiliza GraphQL.</p>\n<h2>Requisitos</h2>\n<p>Primero, se requiere instalar la herramienta de línea de comandos de Gatsby.</p>\n<pre><code class=\"language-bash\">    npm install -g gatsby-cli\n</code></pre>\n<p>Puedes utilizar una plantilla de blog para iniciar el proyecto:</p>\n<pre><code class=\"language-bash\">    gatsby new my-blog-starter https://github.com/gatsbyjs/gatsby-starter-blog\n</code></pre>\n<p>Listo! Puedes iniciar tu blog utilizando:</p>\n<pre><code class=\"language-bash\">   cd my-blog-starter\n   gatsby develop\n</code></pre>","frontmatter":{"title":"Haciendo un blog con Gatsby","author":"Maximiliano Valencia","date":"12 March, 2018"}}},"pathContext":{"slug":"/posts/making-a-blog-with-gatsby/"}}

